import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import { Form, Button } from 'react-bootstrap';

const Formlogin = () => {
    return (
        <section className="d-flex align-items-center full-screen formlogin">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-11 col-md-10 col-xl-8 mx-auto px-4 " >
                        <p className="d-flex justify-content-end">Having Issue? <span className="pl-1"><a href="#"> Get Help</a></span></p>
                        <Form>
                            <h1>Sign In</h1>
                            <p>New Here?<span><a href="#"> Create Account</a></span></p>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label>User Name or Email address</Form.Label>
                                <Form.Control size="lg" type="email" />
                            </Form.Group>
                            <Form.Group controlId="formBasicPassword">
                                <Form.Label>Enter Password </Form.Label> <span className="float-right"><a href="#">Forget Password ?</a></span>
                                <Form.Control size="lg" type="password" />
                            </Form.Group>
                            <Button className="formbtn" variant="primary" type="submit">
                                Sign In
                            </Button>
                        </Form>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Formlogin;
