import React from 'react'
import { Form, Button } from 'react-bootstrap';
import { Row, Col, Image, Container } from 'react-bootstrap';

function Client() {
    return (
        <div className="py-5">
            <div>
                <h1 className="pl-5 font-weight-bold" style={{ fontSize: "25px" }}>Add Client</h1>
            </div>
            <hr></hr>
            <div className="col-10 col-lg-8 m-auto pb-3">
                <Form className="clientform " >
                    <Form.Group as={Row} controlId="bname">
                        <Form.Label column sm={2}>
                            Bussiness Name
                    </Form.Label>
                        <Col sm={10}>
                            <Form.Control type="text" placeholder="Enter Bussiness Name" size="lg" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="bcat">
                        <Form.Label column sm={2}>Bussiness Category</Form.Label>
                        <Col sm={10}>
                            <Form.Control as="select" size="lg">
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                            </Form.Control>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="bsubcat">
                        <Form.Label column sm={2}>Bussiness Sub Category</Form.Label>
                        <Col sm={10}>
                            <Form.Control as="select" defaultValue="" size="lg">
                                <option></option>
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                            </Form.Control>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="bemail">
                        <Form.Label column sm={2}>
                            Business Email
                    </Form.Label>
                        <Col sm={10}>
                            <Form.Control type="email" placeholder="Business Email" size="lg" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="baddress">
                        <Form.Label column sm={2}>
                            Bussiness Address
                    </Form.Label>
                        <Col sm={10}>
                            <Form.Control placeholder="Address Line 1" size="lg" />
                            <br></br>
                            <Form.Control placeholder="Address Line 2" size="lg" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="salesrep">
                        <Form.Label column sm={2}>Sales Rep</Form.Label>
                        <Col sm={10}>
                            <Form.Control as="select" size="lg" defaultValue="Select Sales Rep">
                                <option>Select Sales Rep</option>
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                            </Form.Control>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="pmethod">
                        <Form.Label column sm={2}>
                            Payment Method
                    </Form.Label>
                        <Col sm={10}>
                            <Form.Control type="text" placeholder="Payment Method" size="lg" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="asousing">
                        <Form.Label column sm={2}>
                            Associate using
                    </Form.Label>
                    </Form.Group>
                    <Button type="submit" className="float-right" style={{ fontSize: "22px", color: "black", padding: "14px 26px", backgroundColor: "#add8e6" }}>Add Client</Button>
                </Form>
            </div>
        </div>
    )
}

export default Client;
